body {
    background: purple;
}

.card-list {
    width: 85vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 20px;
}

.card-list__body {
    display: flex;
    flex-direction: column;
    background-color: #95dada;
    border: 1px solid grey;
    border-radius: 5px;
    padding: 25px;
    cursor: pointer;
    -moz-osx-font-smoothing: grayscale;
    backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform 0.25s ease-out;
}

.card-list__body:hover {
    transform: scale(1.05);
}

.search-box {
    width: 100%;
    text-align: center;
    margin: 0;
}

.search {
    height: 50px;
    padding: 10px;
    font-size: 25px;
    margin: 20px;
}